___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}

body, html {

  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;

  font-size: 12px;

  height: 100%;
  padding: 0;
  margin: 0;
}

a:link {
  text-decoration: none;
}

.content,
.content > div {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.content > .message {
  text-align: center;
  display: table;

  font-size: 16px;
  color: #111;
}

.content > .message .note {
  vertical-align: middle;
  text-align: center;
  display: table-cell;
}

.content .error .details {
  max-width: 500px;
  font-size: 12px;
  margin: 20px auto;
  text-align: left;
}

.content .error pre {
  border: solid 1px #CCC;
  background: #EEE;
  padding: 10px;
}

.content:not(.with-error) .error,
.content.with-error .intro,
.content.with-diagram .intro {
  display: none;
}


.content .canvas,
.content.with-error .canvas {
  visibility: hidden;
}

.content.with-diagram .canvas {
  visibility: visible;
}

.buttons {
  position: fixed;
  bottom: 20px;
  left: 20px;

  padding: 0;
  margin: 0;
  list-style: none;
}

.buttons > li {
  display: inline-block;
  margin-right: 10px;
}
.buttons > li > a {
  background: #DDD;
  border: solid 1px #666;
  display: inline-block;
  padding: 5px;
}

.buttons a {
  opacity: 0.3;
}

.buttons a.active {
  opacity: 1.0;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;AACxB;;AAEA;;EAEE,2DAA2D;;EAE3D,eAAe;;EAEf,YAAY;EACZ,UAAU;EACV,SAAS;AACX;;AAEA;EACE,qBAAqB;AACvB;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,cAAc;;EAEd,eAAe;EACf,WAAW;AACb;;AAEA;EACE,sBAAsB;EACtB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;EACtB,gBAAgB;EAChB,aAAa;AACf;;AAEA;;;EAGE,aAAa;AACf;;;AAGA;;EAEE,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,UAAU;;EAEV,UAAU;EACV,SAAS;EACT,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;AACpB;AACA;EACE,gBAAgB;EAChB,sBAAsB;EACtB,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,YAAY;AACd","sourcesContent":["* {\n  box-sizing: border-box;\n}\n\nbody, html {\n\n  font-family: \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n\n  font-size: 12px;\n\n  height: 100%;\n  padding: 0;\n  margin: 0;\n}\n\na:link {\n  text-decoration: none;\n}\n\n.content,\n.content > div {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n\n.content > .message {\n  text-align: center;\n  display: table;\n\n  font-size: 16px;\n  color: #111;\n}\n\n.content > .message .note {\n  vertical-align: middle;\n  text-align: center;\n  display: table-cell;\n}\n\n.content .error .details {\n  max-width: 500px;\n  font-size: 12px;\n  margin: 20px auto;\n  text-align: left;\n}\n\n.content .error pre {\n  border: solid 1px #CCC;\n  background: #EEE;\n  padding: 10px;\n}\n\n.content:not(.with-error) .error,\n.content.with-error .intro,\n.content.with-diagram .intro {\n  display: none;\n}\n\n\n.content .canvas,\n.content.with-error .canvas {\n  visibility: hidden;\n}\n\n.content.with-diagram .canvas {\n  visibility: visible;\n}\n\n.buttons {\n  position: fixed;\n  bottom: 20px;\n  left: 20px;\n\n  padding: 0;\n  margin: 0;\n  list-style: none;\n}\n\n.buttons > li {\n  display: inline-block;\n  margin-right: 10px;\n}\n.buttons > li > a {\n  background: #DDD